import React from 'react'
import PropTypes from 'prop-types'

import './Button.css'

const Button = (props) => {
    return (
        <button className={`btn ${props.className}`}  onClick={props.onClick ? () => props.onClick() : null} >{props.children}</button>
    )
}

export const OutlineButton = props => {
    return (
        <button className={`btn btn-outline ${props.className}`} onClick={props.onClick ? () => props.onClick() : null}>{props.children}</button>
    )
}

Button.propTypes = {
    onClick: PropTypes.func,
    type: PropTypes.string,
    className: PropTypes.string,
    backgroundColor: PropTypes.string.isRequired
}

export default Button